import { BlogContent, BlogPostFile, PublishTarget } from '@youfoundation/transit-lib';
import { useState } from 'react';
import { createPortal } from 'react-dom';
import { t } from '../../../helpers/i18n/dictionary';
import useBlog from '../../../hooks/blogs/useBlog';
import { ChannelDefinitionVm } from '../../../hooks/blogs/useChannels';
import usePortal from '../../../hooks/portal/usePortal';
import ActionButton from '../../Buttons/ActionButton';
import Checkbox from '../../Form/Checkbox';

const PublishDialog = ({
  title,
  confirmText,
  isOpen,
  channels,
  blogFile,
  onConfirm,
  onCancel,
}: {
  title: string;
  confirmText?: string;

  isOpen: boolean;

  channels: ChannelDefinitionVm[];
  blogFile: BlogPostFile<BlogContent>;

  onConfirm: () => void;
  onCancel: () => void;
}) => {
  const target = usePortal('modal-container');
  const { mutate: publishBlog, status: publishBlogStatus } = useBlog().publish;
  const [targets, setTargets] = useState(null);

  if (!isOpen) {
    return null;
  }

  const publishedChannels = blogFile.publishTargets.map((trgt) => {
    const channel = channels.find((chnl) => chnl.channelId === trgt.channelId);
    return channel;
  });

  const unpublishedChannels = channels.reduce((currArray, chnl) => {
    if (publishedChannels.find((publishedChnl) => publishedChnl.channelId === chnl.channelId)) {
      return currArray;
    } else {
      return [...currArray, chnl];
    }
  }, []);

  const dialog = (
    <div className="relative z-50" aria-labelledby="modal-title" role="dialog" aria-modal="true">
      <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity"></div>
      <div className="fixed inset-0 z-10 overflow-y-auto">
        <div className="flex min-h-full items-end justify-center p-4 text-center sm:items-center sm:p-0">
          <div className="relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all dark:bg-black sm:my-8 sm:w-full sm:max-w-lg">
            <div className="bg-white px-4 pt-5 pb-4 dark:bg-black sm:p-6 sm:pb-4">
              <div className="mt-3 text-center sm:mt-0 sm:text-left">
                <h3
                  className="mb-3 text-lg font-medium leading-6 text-gray-900 dark:text-slate-50"
                  id="modal-title"
                >
                  {title}
                </h3>

                {publishedChannels.length ? (
                  <>
                    <div className="-mx-4 mb-5 bg-slate-100 py-10 px-4 sm:-mx-6 sm:px-6">
                      <h2 className="mb-2 text-lg">Already published to:</h2>
                      {publishedChannels.map((channel) => (
                        <PublishedChannel
                          channel={channel}
                          blogFile={blogFile}
                          key={channel.channelId}
                        />
                      ))}
                    </div>
                  </>
                ) : null}

                {unpublishedChannels.length ? (
                  <>
                    {publishedChannels.length ? (
                      <h2 className="mb-2 text-lg">Other Channels:</h2>
                    ) : null}

                    {unpublishedChannels.map((channel) => {
                      return (
                        <div key={channel.channelId} className="mb-2">
                          <Checkbox
                            value={channel.channelId}
                            id={channel.channelId}
                            onChange={(e) => {
                              const newTargets = { ...targets };
                              newTargets[e.target.value] = e.target.checked;
                              setTargets(newTargets);
                            }}
                          />
                          <label htmlFor={channel.channelId} className="ml-2">
                            {channel.name}
                          </label>
                        </div>
                      );
                    })}
                  </>
                ) : null}
              </div>
            </div>

            <div className="-mx-2 px-4 py-3 sm:flex sm:flex-row-reverse sm:px-6">
              <ActionButton
                className="mx-2"
                state={publishBlogStatus}
                onClick={() => {
                  const publishTargets: PublishTarget[] = channels.reduce((currArray, chnl) => {
                    if (targets && targets[chnl.channelId]) {
                      const prevTarget = blogFile.publishTargets.find(
                        (trgt) => trgt.channelId === chnl.channelId
                      );

                      return [
                        ...currArray,
                        {
                          acl: chnl.acl,
                          channelId: chnl.channelId,
                          fileId: prevTarget?.fileId ?? undefined,
                          lastPublishTime: prevTarget?.lastPublishTime ?? undefined,
                        },
                      ];
                    } else {
                      return currArray;
                    }
                  }, []);

                  publishBlog(
                    {
                      blogFile: blogFile,
                      publishTargets: [...blogFile.publishTargets, ...publishTargets],
                    },
                    {
                      onSuccess: () => {
                        onConfirm();
                      },
                    }
                  );
                }}
              >
                {confirmText ?? 'Publish'}
              </ActionButton>
              <ActionButton className="mx-2" type="secondary" onClick={onCancel}>
                {t('Cancel')}
              </ActionButton>
            </div>
          </div>
        </div>
      </div>
    </div>
  );

  return createPortal(dialog, target);
};

const PublishedChannel = ({
  channel,
  blogFile,
}: {
  channel: ChannelDefinitionVm;
  blogFile: BlogPostFile<BlogContent>;
}) => {
  const { mutate: unPublishBlog, status: unPublishBlogStatus } = useBlog().unpublish;

  return (
    <div key={channel.channelId} className="mb-2 flex flex-row">
      <ActionButton
        icon="trash"
        title={t('Unpublish from this channel')}
        className="mr-2 px-2 py-1"
        type="remove"
        onClick={() => unPublishBlog({ blogFile: blogFile, channelId: channel.channelId })}
        state={unPublishBlogStatus}
      />
      <p className="my-auto">{channel.name}</p>
    </div>
  );
};

export default PublishDialog;
